import React, { useEffect, useState } from "react";
import { useLocation, useSearchParams } from "react-router-dom";
import MovieCard from "../../common/MovieCard/MovieCard";
import ReactPaginate from "react-paginate";
import LoadingSpinner from "../../common/LoadingSpinner/LoadingSpinner";
import GenreSlider from "./components/GenreSlider";
import Footer from "../../common/Footer/Footer";
import { genreResponsive } from "../../constants/responsive";
import { useSearchMediaQuery } from "../../hooks/useSearchMedia";
import { useMovieGenreQuery } from "../../hooks/useMovieGenre";
import "./MoviePage.style.css";

const MoviePage = () => {
  const [page, setPage] = useState(1);
  const [query, setQuery] = useSearchParams();
  const keyword = query.get("q");
  const genreParam = query.get('genre');
  const location = useLocation(); //   
  const [selectedGenre, setSelectedGenre] = useState(null);
  // console.log("selectedGenre:", selectedGenre);

  const {
    data: searchData,
    isLoading,
    isError,
    error,
  } = useSearchMediaQuery({ keyword, page, genre:selectedGenre?.id || genreParam });
  // console.log("useSearchMediaQuery's searchData:", searchData);

  const { data:genreData } = useMovieGenreQuery();
  // console.log("useSearchMediaQuery's genreData:", genreData);

  //     
  useEffect(() => {
    if (genreParam) {
      const genre = genreData?.find(g => g.name === genreParam);
      setSelectedGenre(genre);
    }
  }, [genreParam, genreData]);

  //     /movies    
  useEffect(() => {
    if (!genreParam) {
      setSelectedGenre(null);
    }
  }, [location]);

  if (isLoading) {
    return <LoadingSpinner />;
  }

  if (isError) {
    console.log("MoviePage Error Message:", error.message);
  }

  const handlePageClick = ({ selected }) => {
    setPage(selected + 1);
    // console.log("handlePageClick:", page);
  };

  return (
    <div className="movie-container">
      <div className="movie-top">
        <div className="movie-top-info">
          {keyword
            ? `'${keyword}'  :  ${searchData?.total_results}`
            : selectedGenre
              ? `'${selectedGenre.name}'   .`
              : ` !    `}
        </div>
        <GenreSlider
          genres={genreData}
          responsive={genreResponsive}
          selectedGenre={selectedGenre}
          setSelectedGenre={setSelectedGenre}
        />
      </div>
      <div className="movie-bottom">
        <div className="results-area">
          {searchData?.results.map((movie, idx) => (
            <MovieCard key={idx} movie={movie} className="movie-card" />
          ))}
        </div>
        <ReactPaginate
          pageCount={
            searchData?.total_pages > 50 ? 50 : searchData?.total_pages
          } //   
          forcePage={page - 1} //  
          previousLabel=""
          breakLabel="..."
          nextLabel=""
          onPageChange={handlePageClick}
          pageRangeDisplayed={3}
          marginPagesDisplayed={3}
          renderOnZeroPageCount={null}
          containerClassName="pagination" //   css
          pageClassName="page-item" //   (li) css
          previousClassName="page-item prev-btn" //  (li) css
          nextClassName="page-item next-btn" //  (li) css
          breakClassName="page-item" // (...)(li) css
          pageLinkClassName="page-link" //   (a) css
          previousLinkClassName="page-link" //   (a) css
          nextLinkClassName="page-link" //   (a) css
          breakLinkClassName="page-link" // (...) (a) css
          activeClassName="page-active" //    css
          disabledClassName="page-disabled" //   css
        />
      </div>
      <Footer />
    </div>
  );
};

export default MoviePage;
